#section-about-intro {
    width: calc(100% - 80px);
    padding-left: 30px;
    padding-right: 50px;
    height: 100%;
    padding-top: 150px;
}

#article-about-intro {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.article-about-intro {
    width: 70%;
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-weight: 600;
    font-style: normal;
    font-size: 28px;
    text-align: center;
    color: rgba(61, 61, 61);
    line-height: 1.5;
    margin-bottom: 20px;
}

#gael_link {
    color: #036635;
    text-decoration: none;
    font-weight: bold;
}

#MIT_link {
    color: rgba(61, 61, 61);
    font-weight: 600;
}

@media screen and (max-width: 1000px) and (min-width: 600px){
    .article-about-intro {
        width: 100%;
        font-size: 25px;
        line-height: 1.5;
        margin-bottom: 20px;
        text-align: center;
    }
}

@media screen and (max-width: 599px){
    .article-about-intro {
        width: 100%;
        font-size: 23px;
        line-height: 1.2;
        margin-bottom: 20px;
        text-align: left;
    }
}